        "type": "basic",
        "isChildOf": "director",
        "isConnectedwith": "FALSE",
        "text": "<p>“director” means a director of the company, and includes any person occupying the position of director, by whatever name called; </p>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            <h5>APPOINTMENT OF DIRECTORS</h5><h5>Methods of appointing directors</h5><p>17.—(1) Any person who is willing to act as a director, and is permitted by law to do so, may be appointed to be a director—(a) by ordinary resolution, or(b) by a decision of the directors.(2) In any case where, as a result of death, the company has no shareholders and no directors, the personal representatives of the last shareholder to have died have the right, by notice in writing, to appoint a person to be a director.(3) For the purposes of paragraph (2), where 2 or more shareholders die in circumstancesrendering it uncertain who was the last to die, a younger shareholder is deemed to have survived an older shareholder.</p><h5>Termination of director’s appointment</h5><p>18. A person ceases to be a director as soon as—(a) that person ceases to be a director by virtue of any provision of the Companies Act 2006 or is prohibited from being a director by law;(b) a bankruptcy order is made against that person;(c) a composition is made with that person’s creditors generally in satisfaction of thatperson’s debts;(d) a registered medical practitioner who is treating that person gives a written opinion to the company stating that that person has become physically or mentally incapable of acting as a director and may remain so for more than three months;(e) by reason of that person’s mental health, a court makes an order which wholly or partly prevents that person from personally exercising any powers or rights which that person would otherwise have;(f) notification is received by the company from the director that the director is resigning from office, and such resignation has taken effect in accordance with its terms.</p>",
