        "text": "<h5>Calling a directors’ meeting</h5><p> 9.—(1) Any director may call a directors’ meeting by giving notice of the meeting to thedirectors or by authorising the company secretary (if any) to give such notice.(2) Notice of any directors’ meeting must indicate—(a) its proposed date and time;(b) where it is to take place; and(c) if it is anticipated that directors participating in the meeting will not be in the same place, how it is proposed that they should communicate with each other during the meeting.(3) Notice of a directors’ meeting must be given to each director, but need not be in writing.(4) Notice of a directors’ meeting need not be given to directors who waive their entitlement to notice of that meeting, by giving notice to that effect to the company not more than 7 days after the date on which the meeting is held. Where such notice is given after the meeting has been held, that does not affect the validity of the meeting, or of any business conducted at it. </p>",
