        "text": "<h5>Directors’ remuneration</h5><p>19.—(3) Subject to the articles, a director’s remuneration may—(a) take any form, and(b) include any arrangements in connection with the payment of a pension, allowance or gratuity, or any death, sickness or disability benefits, to or in respect of that director.(4) Unless the directors decide otherwise, directors’ remuneration accrues from day to day.</p>",
