        "name": "Quorum of Directors Meeting",
        "type": "basic",
        "isChildOf": "board-of-directors-meeting",
        "isConnectedwith": "FALSE",
        "text": "<h5>Quorum for directors’ meetings</h5><p> 11.—(1) At a directors’ meeting, unless a quorum is participating, no proposal is to be voted on, except a proposal to call another meeting.(2) The quorum for directors’ meetings may be fixed from time to time by a decision of the directors, but it must never be less than two, and unless otherwise fixed it is two.(3) If the total number of directors for the time being is less than the quorum required, thedirectors must not take any decision other than a decision—(a) to appoint further directors, or(b) to call a general meeting so as to enable the shareholders to appoint further directors.</p>",
