        "isConnectedwith": "2pnwfvdxiq",
        "text": "<h5> Unanimous decisions<h5>   <p>8.—(2) Such a decision may take the form of a resolution in writing, copies of which have been signed by each eligible director or to which each eligible director has otherwise indicated agreement in writing.</p>",
        "document": "Articles of Association",
