        "type": "basic",
        "isChildOf": "permitted-decision-making-in-conflict-of-interest",
        "isConnectedwith": "FALSE",
        "text": "<h5>Conflicts of interest</h5><p>(4) For the purposes of this article, the following are permitted causes—(a) a guarantee given, or to be given, by or to a director in respect of an obligation incurred by or on behalf of the company or any of its subsidiaries;(b) subscription, or an agreement to subscribe, for shares or other securities of the company or any of its subsidiaries, or to underwrite, sub-underwrite, or guarantee subscription for any such shares or securities; and(c) arrangements pursuant to which benefits are made available to employees and directors or former employees and directors of the company or any of its subsidiaries which do not provide special benefits for directors or former directors.</p>",
