        "text": "<h5>Quorum for directors’ meetings</h5><p>11.—(3) If the total number of directors for the time being is less than the quorum required, thedirectors must not take any decision other than a decision—(a) to appoint further directors, or(b) to call a general meeting so as to enable the shareholders to appoint further directors.</p>",
