        "text": "<h5>Procedure for declaring dividends</h5><p>30.—1) The company may by ordinary resolution declare dividends, and </mark> the directors may decide to pay interim dividends.</mark> (6) <mark> The directors may pay at intervals any dividend payable at a fixed rate if it appears to them that the profits available for distribution justify the payment.</mark> </p>                                                                                                                                                                                                                              ",
