      {
        "N": "67",
        "id": "62hiflxtkf",
        "name": "Director's Insurance",
        "type": "basic",
        "isChildOf": "director",
        "isConnectedwith": "FALSE",
        "text": "<h5>Insurance</h5><p>53.—(1) The directors may decide to purchase and maintain insurance, at the expense of the company, for the benefit of any relevant director in respect of any relevant loss.(2) In this article—(a) a “relevant director” means any director or former director of the company or an associated company,(b) a “relevant loss” means any loss or liability which has been or may be incurred by a relevant director in connection with that director’s duties or powers in relation to the company, any associated company or any pension fund or employees’ share scheme of the company or associated company, and(c) companies are associated if one is a subsidiary of the other or both are subsidiaries of the same body corporate.</p>",
        "document": "Articles of Association",
        "Conflict comment": "FALSE",
        "Missing comment": "FALSE"
      },
      {
        "N": "68",
        "id": "1vwy07g698",
        "name": "Termination of Appointment",
        "type": "basic",
        "isChildOf": "director's-appointment",
        "isConnectedwith": "FALSE",
        "text": "<h5>Termination of director’s appointment</h5><p>18. A person ceases to be a director as soon as—(a) that person ceases to be a director by virtue of any provision of the Companies Act 2006 or is prohibited from being a director by law;(b) a bankruptcy order is made against that person;(c) a composition is made with that person’s creditors generally in satisfaction of that person’s debts;(d) a registered medical practitioner who is treating that person gives a written opinion to the company stating that that person has become physically or mentally incapable of acting as a director and may remain so for more than three months;(e) by reason of that person’s mental health, a court makes an order which wholly or partly prevents that person from personally exercising any powers or rights which that person would otherwise have;(f) notification is received by the company from the director that the director is resigning from office, and such resignation has taken effect in accordance with its terms.</p>",
        "document": "Articles of Association",
        "Conflict comment": "FALSE",
        "Missing comment": "FALSE"
      }
