    "name": "Director's Decision",
    "type": "basic",
    "isChildOf": "director's-decision-making",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Directors to take decisions collectively</h5><p>7.—(1) The general rule about decision-making by directors is that any decision of the directors must be either a majority decision at a meeting or a decision taken in  accordance with article 8.(2) If—(a) the company only has one director, and(b) no provision of the articles requires it to have more than one director,the general rule does not apply, and the director may take decisions without regard to any of the provisions of the articles relating to directors’ decision-making. </p>                                                                                                                                                                                               <h5> Unanimous decisions<h5><p>8.—(1) A decision of the directors is taken in accordance with this article when all eligible directors indicate to each other by any means that they share a common view on a matter.(2)<mark> Such a decision may take the form of a resolution in writing, copies of which have been signed by each eligible director or to which each eligible director has otherwise indicated agreement in writing.<mark>(3) References in this article to eligible directors are to directors who would have been entitled to vote on the matter had it been proposed as a resolution at a directors’ meeting.(4) A decision may not be taken in accordance with this article if the eligible directors would not have formed a quorum at such a meeting.</p>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            <h5>Records of decisions to be kept</h5><p>15. The directors must ensure that the company keeps a record, in writing, for at least 10 years from the date of the decision recorded, of every unanimous or majority decision taken by the directors.</p>",
    "document": "Shareholders Agreement 1",
    "text ShA": "<h5>10. The Board</h5><p>10.2 Any question arising at any meeting of the Board, other than ReservedMatters, shall be decided by a majority of votes of the directors present.10.3 The Board will be entitled to resolve matters by the written resolution of all thedirectors and may establish committees in accordance with the Articles.</p>",
