    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Methods of appointing directors</h5><p>17.—(2) In any case where, as a result of death, the company has no shareholders and no directors, the personal representatives of the last shareholder to have died have the right, by notice in writing, to appoint a person to be a director.(3) For the purposes of paragraph (2), where 2 or more shareholders die in circumstances rendering it uncertain who was the last to die, a younger shareholder is deemed to have survived an older shareholder.</p>",
