    "undefined": "30",
    "id": "iufawoda9n",
    "name": "Termination of Appointment",
    "type": "basic",
    "isChildOf": "director's-appointment",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Termination of director’s appointment</h5><p>18. A person ceases to be a director as soon as—(a) that person ceases to be a director by virtue of any provision of the Companies Act 2006 or is prohibited from being a director by law;(b) a bankruptcy order is made against that person;(c) a composition is made with that person’s creditors generally in satisfaction of thatperson’s debts;(d) a registered medical practitioner who is treating that person gives a written opinion to the company stating that that person has become physically or mentally incapable of acting as a director and may remain so for more than three months;(e) by reason of that person’s mental health, a court makes an order which wholly or partly prevents that person from personally exercising any powers or rights which that person would otherwise have;(f) notification is received by the company from the director that the director is resigning from office, and such resignation has taken effect in accordance with its terms.</p>",
    "document": "Shareholders Agreement 1",
    "text ShA": "<h5>14. Reserved Matters</h5>                                                                                                                                                                                                                                                                                                                                                                                                                                                                       <p> 14.2 The Shareholders shall procure that save as contemplated by this Agreementthe Company shall not without either passing a special resolution approvingthe action at a Shareholder meeting or obtaining the prior written consent of75% of the holders of shares representing <mark> not less than 75% of the total votingrights of eligible Shareholders</mark>:14.2.1 appoint or <mark>remove</mark>  any director of the Company;</p>",
    "Conflict comment": "FALSE",
    "Missing comment": "FALSE"
