    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Directors’ remuneration</h5><p>19.—(1) Directors may undertake any services for the company that the directors decide.(2) Directors are entitled to such remuneration as the directors determine—(a) for their services to the company as directors, and(b) for any other service which they undertake for the company.</p>",
