    "text AoA": "<h5>Unanimous decisions</h5><p>8.—(1) A decision of the directors is taken in accordance with this article when all eligible directors indicate to each other by any means that they share a common view on a matter.(2) Such a decision may take the form of a resolution in writing, copies of which have been signed by each eligible director or to which each eligible director has otherwise indicated agreement in writing.(3) References in this article to eligible directors are to directors who would have been entitled to vote on the matter had it been proposed as a resolution at a directors’ meeting.(4) A decision may not be taken in accordance with this article if the eligible directors would not have formed a quorum at such a meeting.</p>",
