    "text AoA": "<h5>Conflicts of interest</h5><p>14.—(1) If a proposed decision of the directors is concerned with an actual or proposed transaction or arrangement with the company in which a director is interested, that director is not to be counted as participating in the decision-making process for quorum or voting purposes.</p>",
