    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Conflicts of interest</h5><p>14.—(1) If a proposed decision of the directors is concerned with an actual or proposed transaction or arrangement with the company in which a director is interested, that director is not to be counted as participating in the decision-making process for quorum or voting purposes.(5) For the purposes of this article, references to proposed decisions and decision-makingprocesses include any directors’ meeting or part of a directors’ meeting.(6) Subject to paragraph (7), if a question arises at a meeting of directors or of a committee of directors as to the right of a director to participate in the meeting (or part of the meeting) for voting or quorum purposes, the question may, before the conclusion of the meeting, be referred to the chairman whose ruling in relation to any director other than the chairman is to be final and conclusive.(7) If any question as to the right to participate in the meeting (or part of the meeting) should arise in respect of the chairman, the question is to be decided by a decision of the directors at that meeting, for which purpose the chairman is not to be counted as participating in the meeting (or that part of the meeting) for voting or quorum purposes.</p>",
