    "name": "Permitted Decision-making in Conflict of Interest",
    "type": "basic",
    "isChildOf": "conflict-of -interest",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Conflicts of interest</h5><p>14.—(2) But if paragraph (3) applies, a director who is interested in an actual or proposed transaction or arrangement with the company is to be counted as participating in the decision-making process for quorum and voting purposes.(3) This paragraph applies when—(a) the company by ordinary resolution disapplies the provision of the articles which would otherwise prevent a director from being counted as participating in the decision-making process;(b) the director’s interest cannot reasonably be regarded as likely to give rise to a conflict of interest; or(c) the director’s conflict of interest arises from a permitted cause.(4) For the purposes of this article, the following are permitted causes—(a) a guarantee given, or to be given, by or to a director in respect of an obligation incurred by or on behalf of the company or any of its subsidiaries;(b) subscription, or an agreement to subscribe, for shares or other securities of the company or any of its subsidiaries, or to underwrite, sub-underwrite, or guarantee subscription for any such shares or securities; and(c) arrangements pursuant to which benefits are made available to employees and directors or former employees and directors of the company or any of its subsidiaries which do not provide special benefits for directors or former directors.</p>",
