    "text AoA": "<h5>Quorum for directors’ meetings</h5><p>11.—(3) If the total number of directors for the time being is less than the quorum required, thedirectors must not take any decision other than a decision—(a) to appoint further directors, or(b) to call a general meeting so as to enable the shareholders to appoint further directors.</p>",
    "document": "Shareholders Agreement 1",
    "text ShA": "<h5>10. The Board</h5><p>10.5 The quorum necessary to constitute a meeting of the Board shall be two directors present in person. <mark>If a quorum is not present within thirty minutes (or such longer time as the chairman of the Board may allow) of the time appointed for a meeting of the Board, the meeting shall be adjourned to the same time and place on the seventh day following the original meeting. If at the adjourned meeting a quorum is not present within fifteen minutes from the time appointed for the meeting (unless otherwise agreed by directors representing at least three quarters of their number) the adjourned meeting shall be dissolved.</mark></p>",
    "Conflict comment": "FALSE",
