    "name": "Chairing at General Meeting",
    "type": "basic",
    "isChildOf": "chairman-of-board-of-directors",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Chairing general meetings</h5><p>39.—(1) If the directors have appointed a chairman, the chairman shall chair general meetings if present and willing to do so.(2) If the directors have not appointed a chairman, or if the chairman is unwilling to chair the meeting or is not present within ten minutes of the time at which a meeting was due to start—(a) the directors present, or(b) (if no directors are present), the meeting,must appoint a director or shareholder to chair the meeting, and the appointment of the chairman of the meeting must be the first business of the meeting.(3) The person chairing a meeting in accordance with this article is referred to as “the chairman of the meeting”.</p>",
    "document": "Shareholders Agreement 1",
    "text ShA": "FALSE",
    "Conflict comment": "FALSE",
    "Missing comment": "FALSE"
