    "isChildOf": "director-powers",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Committees</h5><p>6.—(1) Committees to which the directors delegate any of their powers must follow procedures which are based as far as they are applicable on those provisions of the articles which govern the taking of decisions by directors.<mark>(2) The directors may make rules of procedure for all or any committees, which prevail over rules derived from the articles if they are not consistent with them.</mark></p>",
    "document": "Shareholders Agreement 1",
    "text ShA": "<h5>10. The Board</h5> <p>10.3 The Board will be entitled to resolve matters by the written resolution of all the directors and <mark> may establish committees in accordance with the Articles.</mark></p>",
