    "text ShA": "<h5>13. Management</h5><p>13.2 The directors shall cause to be prepared for approval by the Board an AnnualBusiness Plan in respect of each Financial Year of the Company outlining theproposed objectives of the business of the Company and containing cash flowprojections and an operating budget for that Financial Year. The directorsshall, not later than 60 days before the beginning of each Financial Year of theCompany, consider the Annual Business Plan for the following Financial Yearand deliver the same to the Board together with such other information as theBoard may request for the purpose of evaluating the Annual Business Plan.The Annual Business Plan for the first Financial Year shall be in the formadopted by the Board on the date hereof.</p>",
