    "text ShA": "<h5>13. Management</h5><p>13.4 [Certain of the Shareholders wish to be eligible for relief under the EnterpriseInvestment Scheme and for Reinvestment/Rollover Relief in respect of theirinvestment in Ordinary Shares and accordingly the Board will endeavour, in sofar as it is commercially reasonable and having regard to the best interests ofthe Company, to conduct the business activities of the Company in a mannerconsistent with the Company's status as a qualifying company for the purposeof relief under the Enterprise Investment Scheme and Reinvestment/RolloverRelief. The Board will cooperate with Shareholders whose shares are eligiblefor the reliefs referred to above and will assist in providing whatever returnsand certificates may reasonably be required to facilitate the Shareholders'claims for such relief.] </p>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      ",
