    "undefined": "18",
    "id": "7x8yfmpth5",
    "name": "Director's Insurance",
    "type": "basic",
    "isChildOf": "director",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Insurance</h5><p>53.—(1) The directors may decide to purchase and maintain insurance, at the expense of the company, for the benefit of any relevant director in respect of any relevant loss.(2) In this article—(a) a “relevant director” means any director or former director of the company or an associated company,(b) a “relevant loss” means any loss or liability which has been or may be incurred by a relevant director in connection with that director’s duties or powers in relation to the company, any associated company or any pension fund or employees’ share scheme of the company or associated company, and(c) companies are associated if one is a subsidiary of the other or both are subsidiaries of the same body corporate.</p>",
