    "undefined": "26",
    "id": "ybyhbc8u7k",
    "name": "Participation on Board of Director Meeting",
    "type": "basic",
    "isChildOf": "board-of-directors-meeting",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Participation in directors’ meetings</h5><p>10.—(1) Subject to the articles, directors participate in a directors’ meeting, or part of adirectors’ meeting, when—(a) the meeting has been called and takes place in accordance with the articles, and(b) they can each communicate to the others any information or opinions they have on any particular item of the business of the meeting.(2) In determining whether directors are participating in a directors’ meeting, it is irrelevant where any director is or how they communicate with each other.(3) If all the directors participating in a meeting are not in the same place, they may decide that the meeting is to be treated as taking place wherever any of them is.</p>",
    "document": "Shareholders Agreement 1",
    "text ShA": "<h5>10. The Board</h5><p>10.4. Meetings of the Board shall take place at such time or times as may berequired or as requested by any of the directors but not in any event atintervals of more than <Insert number of months> months calculatedfrom the date hereof. Unless otherwise agreed in writing by all the directors, atleast <Insert number of days> Clear Days’ notice in writing shall begiven of each meeting of the Board. Such notice shall specify in as great adetail as is practicable the business to be considered at the meeting and,unless all the directors (or their duly appointed alternates) agree otherwise, nomatters shall be resolved at any meeting of the Board except those specifiedin the notice of the meeting.<mark> [Directors may attend meetings of the Board byany means authorised by the Articles.]</mark></p>",
    "Conflict comment": "FALSE",
