    "undefined": "27",
    "id": "ps0yi16juj",
    "name": "Chairman of Board of Directors",
    "type": "basic",
    "isChildOf": "board-of-directors-meeting",
    "isConnectedwith": "zi21yxn6q1",
    "text AoA": "<h5>Chairing of directors’ meetings</h5><p>12.—(1) The directors may appoint a director to chair their meetings.(2) The person so appointed for the time being is known as the chairman.(3) The directors may terminate the chairman’s appointment at any time.(4) If the chairman is not participating in a directors’ meeting within ten minutes of the time at which it was to start, the participating directors must appoint one of themselves to chair it.</p><h5>Casting vote</h5><p>13.—(1) If the numbers of votes for and against a proposal are equal, the chairman or other director chairing the meeting has a casting vote.(2) But this does not apply if, in accordance with the articles, the chairman or other director is not to be counted as participating in the decision-making process for quorum or voting purposes.</p>",
    "document": "Shareholders Agreement 1",
    "text ShA": "<h5>10. The Board</h5><p>10.1 Unless otherwise agreed by the Board:10.1.1 those listed in Schedule 4 will be appointed as the directors of theCompany pursuant to the Articles; and<mark>10.1.2 <Name> shall be appointed as the first chairman of the Boardpursuant to the Articles.</mark></p>",
    "Conflict comment": "FALSE",
