    "text AoA": "<h5>Directors’ remuneration</h5><p>19.—(5) Unless the directors decide otherwise, directors are not accountable to the company for any remuneration which they receive as directors or other officers or employees of the company’s subsidiaries or of any other body corporate in which the company is interested.</p>",
