    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Directors’ expenses</h5><p>20. The company may pay any reasonable expenses which the directors properly incur inconnection with their attendance at—(a) meetings of directors or committees of directors,(b) general meetings, or(c) separate meetings of the holders of any class of shares or of debentures of the company, or otherwise in connection with the exercise of their powers and the discharge of their responsibilities in relation to the company.</p>",
