    "name": "Shareholders' reserve power in Director Authority",
    "type": "basic",
    "isChildOf": "director-authority",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Shareholders’ reserve power</h5><p>4.—(1) The shareholders may, by special resolution, direct the directors to take, or refrain from taking, specified action.(2) No such special resolution invalidates anything which the directors have done before the passing of the resolution.</p>",
