    "undefined": "43",
    "id": "kc9s5azebb",
    "name": "Director Obligations",
    "type": "basic",
    "isChildOf": "director-authority",
    "isConnectedwith": "FALSE",
    "text AoA": "FALSE",
    "document": "Shareholders Agreement 1",
    "text ShA": "<h5>13. Management</h5><p>13.1 The directors shall be responsible for the day to day administration andmanagement of the affairs of the Company within the terms of the AnnualBusiness Plan. The directors will perform such duties as may be delegated tothem by the Board subject to their obligation to comply with all decisions anddirections of the Board.The directors shall be required to report to the Boardwith such frequency and in such manner as may be required by the Board.13.2 <mark>The directors shall cause to be prepared for approval by the Board an AnnualBusiness Plan in respect of each Financial Year of the Company outlining theproposed objectives of the business of the Company and containing cash flowprojections and an operating budget for that Financial Year. The directorsshall, not later than 60 days before the beginning of each Financial Year of theCompany, consider the Annual Business Plan for the following Financial Yearand deliver the same to the Board together with such other information as theBoard may request for the purpose of evaluating the Annual Business Plan.The Annual Business Plan for the first Financial Year shall be in the formadopted by the Board on the date hereof.</mark>13.3 <mark>The directors shall be responsible for preparing periodic managementaccounts in respect of consecutive periods of not more than three monthscontaining such financial statements and reports as may be required by theBoard and such management accounts shall be delivered to all Shareholdersand all members of the Board within 30 days after the end of the relevantperiod.</mark>13.4 [Certain of the Shareholders wish to be eligible for relief under the EnterpriseInvestment Scheme and for Reinvestment/Rollover Relief in respect of theirinvestment in Ordinary Shares and accordingly the Board will endeavour, in sofar as it is commercially reasonable and having regard to the best interests ofthe Company, to conduct the business activities of the Company in a mannerconsistent with the Company's status as a qualifying company for the purposeof relief under the Enterprise Investment Scheme and Reinvestment/RolloverRelief. The Board will cooperate with Shareholders whose shares are eligiblefor the reliefs referred to above and will assist in providing whatever returnsand certificates may reasonably be required to facilitate the Shareholders'claims for such relief.] </p>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      <p>“Annual Business Plan” means, a plan prepared by the directors of the Company,in respect of each Financial Year of the Company,outlining the proposed objectives of the business of theCompany, containing cash flow projections and anoperating budget for that Financial Year;\"</p>",
    "Conflict comment": "FALSE",
