    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Records of decisions to be kept</h5><p>15. The directors must ensure that the company keeps a record, in writing, for at least 10 years from the date of the decision recorded, of every unanimous or majority decision taken by the directors. </p>",
