    "isChildOf": "conflict-of -interest",
    "isConnectedwith": "ps0yi16juj",
    "text AoA": "<h5>Conflicts of interest</h5><p>14.—(7) If any question as to the right to participate in the meeting (or part of the meeting) should arise in respect of the chairman, the question is to be decided by a decision of the directors at that meeting, for which purpose the chairman is not to be counted as participating in the meeting (or that part of the meeting) for voting or quorum purposes.</p>",
    "document": "Shareholders Agreement 1",
