    "isChildOf": "chairman-of-board-of-directors",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Chairing of directors’ meetings</h5><p>12.—(1) The directors may appoint a director to chair their meetings.(2) The person so appointed for the time being is known as the chairman.</p>",
