    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Casting vote</h5><p>13.—(1) If the numbers of votes for and against a proposal are equal, the chairman or other director chairing the meeting has a casting vote.(2) But this does not apply if, in accordance with the articles, the chairman or other director is not to be counted as participating in the decision-making process for quorum or voting purposes.</p>",
