    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Procedure for declaring dividends</h5><p>30.—(2) <mark> A dividend must not be declared unless the directors have made a recommendation as to its amount. </mark> Such a dividend must not exceed the amount recommended by the directors.</p>                                                                                                                                                                                                                           ",
    "document": "Shareholders Agreement 1",
