    "type": "basic",
    "isChildOf": "director-powers",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Powers to issue different classes of share</h5><p>22.—(2) The company may issue shares which are to be redeemed, or are liable to be redeemed at the option of the company or the holder, and the directors may determine the terms, conditions and manner of redemption of any such shares.</p>",
    "document": "Shareholders Agreement 1",
    "text ShA": "FALSE",
