    "id": "zxpspconjk",
    "name": "Approval of Share Transfer Form",
    "type": "basic",
    "isChildOf": "director-powers",
    "isConnectedwith": "FALSE",
    "text AoA": "<h5>Share transfers</h5>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                   <p>26.— <mark>(1) Shares may be transferred by means of an instrument of transfer in any usual form or any other form approved by the directors, which is executed by or on behalf of the transferor. </mark>(2) No fee may be charged for registering any instrument of transfer or other document relating to or affecting the title to any share.(3) The company may retain any instrument of transfer which is registered.(4) The transferor remains the holder of a share until the transferee’s name is entered in the register of members as holder of it.(5)  <mark>The directors may refuse to register the transfer of a share, and if they do so, the instrument of transfer must be returned to the transferee with the notice of refusal unless they suspect that the proposed transfer may be fraudulent. </mark> </p>                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                       ",
